.programs {
  background-color: $color-secondary-beige;
}

.programs .swiper-slide {
  border-radius: 12px;

  @include vp-1199 {
    max-width: 324px;
  }

  @include vp-767 {
    max-width: none;
    min-height: 400px;
  }
}

.programs__container {
  position: relative;
  max-width: $max-width;
  margin: 0 auto;
  padding: 124px 100px 182px 100px;

  @include vp-1199 {
    padding: 102px 0 162px 45px;
  }

  @include vp-767 {
    padding: 70px 15px 139px 15px;
  }
}

.programs__content-container {
  min-height: 420px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 71% 51px 20px 20px;
  height: 100%;

  @include vp-1199 {
    padding-top: 88%;
    padding-right: 41px;
  }

  @include vp-767 {
    min-height: auto;
    padding: 93% 10px 18px 10px;
  }
}

.programs__first-slide {
  background-image:
    linear-gradient(to top, rgba(49, 109, 194, 0.2)),
    image-set(url("../img/programs/first-photo/first-photo-desktop.webp") 1dppx,
      url("../img/programs/first-photo/first-photo-desktop@2x.webp") 2dppx,
      url("../img/programs/first-photo/first-photo-desktop.png") 1dppx,
      url("../img/programs/first-photo/first-photo-desktop@2x.png") 2dppx,
    );
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-color: #668bb2;

  @include vp-1199 {
    background-image:
      linear-gradient(to top, rgba(49, 109, 194, 0.2)),
      image-set(url("../img/programs/first-photo/first-photo-tablet.webp") 1dppx,
      url("../img/programs/first-photo/first-photo-tablet@2x.webp") 2dppx,
      url("../img/programs/first-photo/first-photo-tablet.png") 1dppx,
      url("../img/programs/first-photo/first-photo-tablet@2x.png") 2dppx,
    );
  }

  @include vp-767 {
    background-image:
      linear-gradient(to top, rgba(49, 109, 194, 0.2)),
      image-set(url("../img/programs/first-photo/first-photo-mobile.webp") 1dppx,
      url("../img/programs/first-photo/first-photo-mobile@2x.webp") 2dppx,
      url("../img/programs/first-photo/first-photo-mobile.png") 1dppx,
      url("../img/programs/first-photo/first-photo-mobile@2x.png") 2dppx,
      );
  }
}

.programs__second-slide {
  background-image:
    linear-gradient(to top, rgba(49, 109, 194, 0.2)),
    image-set(url("../img/programs/second-photo/second-photo-desktop.webp") 1dppx,
  url("../img/programs/second-photo/second-photo-desktop@2x.webp") 2dppx,
  url("../img/programs/second-photo/second-photo-desktop.png") 1dppx,
  url("../img/programs/second-photo/second-photo-desktop@2x.png") 2dppx,
    );
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-color: #4d7da4;

  @include vp-1199 {
    background-image:
      linear-gradient(to top, rgba(49, 109, 194, 0.2)),
      image-set(url("../img/programs/second-photo/second-photo-tablet.webp") 1dppx,
    url("../img/programs/second-photo/second-photo-tablet@2x.webp") 2dppx,
    url("../img/programs/second-photo/second-photo-tablet.png") 1dppx,
    url("../img/programs/second-photo/second-photo-tablet@2x.png") 2dppx,
      );
  }

  @include vp-767 {
    background-image:
      linear-gradient(to top, rgba(49, 109, 194, 0.2)),
      image-set(url("../img/programs/second-photo/second-photo-mobile.webp") 1dppx,
    url("../img/programs/second-photo/second-photo-mobile@2x.webp") 2dppx,
    url("../img/programs/second-photo/second-photo-mobile.png") 1dppx,
    url("../img/programs/second-photo/second-photo-mobile@2x.png") 2dppx,
      );
  }
}

.programs__third-slide {
  background-image:
    linear-gradient(to top, rgba(49, 109, 194, 0.2)),
    image-set(url("../img/programs/third-photo/third-photo-desktop.webp") 1dppx,
  url("../img/programs/third-photo/third-photo-desktop@2x.webp") 2dppx,
  url("../img/programs/third-photo/third-photo-desktop.png") 1dppx,
  url("../img/programs/third-photo/third-photo-desktop@2x.png") 2dppx,
    );
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-color: #9eb0bc;

  @include vp-1199 {
    background-image:
      linear-gradient(to top, rgba(49, 109, 194, 0.2)),
      image-set(url("../img/programs/third-photo/third-photo-tablet.webp") 1dppx,
    url("../img/programs/third-photo/third-photo-tablet@2x.webp") 2dppx,
    url("../img/programs/third-photo/third-photo-tablet.png") 1dppx,
    url("../img/programs/third-photo/third-photo-tablet@2x.png") 2dppx,
      );
  }

  @include vp-767 {
    background-image:
      linear-gradient(to top, rgba(49, 109, 194, 0.2)),
      image-set(url("../img/programs/third-photo/third-photo-mobile.webp") 1dppx,
    url("../img/programs/third-photo/third-photo-mobile@2x.webp") 2dppx,
    url("../img/programs/third-photo/third-photo-mobile.png") 1dppx,
    url("../img/programs/third-photo/third-photo-mobile@2x.png") 2dppx,
      );
  }
}

.programs h2 {
  font-size: 50px;
  font-weight: 400;
  line-height: 50px;
  color: $color-secondary;
  margin: 0 20% 46px 0;
  margin-bottom: 46px;

  @include vp-1199 {
    font-size: 34px;
    margin-right: 40%;
    margin-bottom: 42px;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 20px;
    margin-right: 25%;
    margin-bottom: 32px;
  }
}

.programs__link {
  position: absolute;
  top: 119px;
  right: 100px;
  width: 234px;

  @include vp-1199 {
    top: 100px;
    right: 45px;
    padding-left: 30px;
  }

  @include vp-767 {
    background-color: $color-default-white;
    top: auto;
    right: auto;
    bottom: 4.1em;
    left: 15px;
    width: 203px;
  }
}

.programs__title {
  position: relative;
}

.programs__title p {
  font-size: 24px;
  font-weight: 400;
  line-height: 26px;
  color: $color-secondary;
  margin: 0;
  margin-right: 127px;
  padding: 18px 12px 0 18px;
  border-radius: 12px 12px 0 0;
  background-color: $color-default-white;
  word-wrap: break-word;

  @include vp-1199 {
    font-size: 20px;
    line-height: 24px;
    margin-right: 90px;
    padding-top: 13px;
  }

  @include vp-767 {
    font-size: 16px;
    line-height: 18px;
    margin-right: 124px;
    padding-top: 20px;
    padding-bottom: 6px;
  }
}

.programs__description {
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 16px;
  margin-top: -1px;
  padding: 12px 18px;
  min-height: 79px;
  border-radius: 0 12px 12px 12px;
  background-color: $color-default-white;

  @include vp-1199 {
    padding: 9px 18px;
  }

  @include vp-767 {
    min-height: 69px;
  }
}

.programs__description p {
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  margin: 0;

  @include vp-1199 {
    line-height: 20px;
  }

  @include vp-767 {
    font-size: 12px;
    line-height: 14px;
  }
}

.programs__auxiliary-icon {
  fill: $color-default-white;
  position: absolute;
  bottom: 0;
  right: 110px;

  @include vp-1199 {
    right: 72px;
  }

  @include vp-767 {
    right: 107px;
  }
}

.programs__buttons-container {
  display: flex;
  gap: 12px;
  width: fit-content;
  position: absolute;
  right: 100px;
  bottom: 7.5em;

  @include vp-1199 {
    right: 45px;
    bottom: 6.3em;
  }

  @include vp-767 {
    right: 15px;
    bottom: auto;
    top: 4.2em;
  }
}
